.App {
  width: 80%;
  margin: 0 auto;
  padding: 50px 0;
}

/* HEADER TRANSPARENCY*/
.ui.inverted.menu {
    background: rgba(0, 0, 0, 0);
}

/* HEADER FONT COLOR */
.ui.secondary.inverted.pointing.menu .item {
  color: #1b1c1d;
}

.ui.secondary.inverted.menu .link.item, .ui.secondary.inverted.menu a.item {
  color: rgba(0,0,0,.87)!important;
}

.ui.secondary.inverted.menu .active.item {
  color: rgba(0,0,0,.87)!important;
}

.ui.inverted.menu .item, .ui.inverted.menu .item>a:not(.ui) {
  color: rgba(0,0,0,.87)
}

.a {
  color: rgba(0,0,0,.87)!important;
}

.member_profile_container {
	display: grid;
	grid-template-columns: 1fr 4fr;
}

.section_profile_container {
	display: grid;
	grid-template-columns: 2fr 4fr 2fr;
}

.section_info {
  padding-right: 30px;
}

.section_desc {
  padding-right: 30px;
  padding-left: 30px;
}

.section_info_card {
  padding-left: 30px;
}

.home_top_header {
  height: 600px;
  background-image: url('https://images.unsplash.com/photo-1521623866472-d27232650c9e?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=6704c159d61c6a995fb741b0ee898d88&auto=format&fit=crop&w=1540&q=80');
  background-position: center;
  background-size: cover;
}

.section_container {
  margin-top: 30px;
}

.search_form {
	width: 25%;
	margin: 0 auto;
  margin-top: 30px;
	padding: 100px 0;
}

.section_profile_topheader {
  height: 300px;
  background-image: url('https://images.unsplash.com/photo-1534511902651-6ab0ce131f2a?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=f1ff38d33bc2a28f5c08b7936e12c239&auto=format&fit=crop&w=1489&q=80');
  background-position: center;
  background-size: cover;
}

.ui.modal {
  top: 10%;
}

.ui.menu {
	margin: 0;
}

.ui.icon.input input {
	width: 100%;
}

.ui.large.input {
  width: 100%;
}

.stripe-connect {
  display: inline-block;
  margin-bottom: 1px;

  background-image: -webkit-linear-gradient(#28A0E5, #015E94);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#28A0E5), to(#015E94));
  background-image: -o-linear-gradient(#28A0E5, #015E94);
  background-image: linear-gradient(#28A0E5, #015E94);

  -webkit-font-smoothing: antialiased;
  border: 0;
  padding: 1px;
  height: 30px;
  text-decoration: none;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);

  cursor: pointer;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.stripe-connect span {
  display: block;
  position: relative;
  padding: 0 12px 0 44px;
  height: 30px;

  background: #1275FF;
  background-image: -webkit-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#7DC5EE), color-stop(85%, #008CDD), to(#30A2E4));
  background-image: -o-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  background-image: linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);

  font-size: 14px;
  line-height: 30px;
  color: white;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border-radius: 3px;
}

.stripe-connect span:before {
  content: '';
  display: block;
  position: absolute;
  left: 11px;
  top: 50%;
  width: 23px;
  height: 24px;
  margin-top: -12px;
  background-repeat: no-repeat;
  background-size: 23px 24px;
}

.stripe-connect:active {
  background: #005D93;
}

.stripe-connect:active span {
  color: #EEE;

  background: #008CDD;
  background-image: -webkit-linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#008CDD), color-stop(85%, #008CDD), to(#239ADF));
  background-image: -o-linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  background-image: linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  -webkit-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
}

.stripe-connect:active span:before {

}

.stripe-connect.light-blue {
  background: #b5c3d8;
  background-image: -webkit-linear-gradient(#b5c3d8, #9cabc2);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#b5c3d8), to(#9cabc2));
  background-image: -o-linear-gradient(#b5c3d8, #9cabc2);
  background-image: linear-gradient(#b5c3d8, #9cabc2);
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}

.stripe-connect.light-blue span {
  color: #556F88;
  text-shadow: 0 1px rgba(255, 255, 255, 0.8);

  background: #f0f5fa;
  background-image: -webkit-linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f0f5fa), color-stop(85%, #e4ecf5), to(#e7eef6));
  background-image: -o-linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  background-image: linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  -webkit-box-shadow: inset 0 1px 0 #fff;
  box-shadow: inset 0 1px 0 #fff;
}

.stripe-connect.light-blue:active {
    background: #9babc2;
}

.stripe-connect.light-blue:active span {
  color: #556F88;
  text-shadow: 0 1px rgba(255, 255, 255, 0.8);

  background: #d7dee8;
  background-image: -webkit-linear-gradient(#d7dee8, #e7eef6);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d7dee8), to(#e7eef6));
  background-image: -o-linear-gradient(#d7dee8, #e7eef6);
  background-image: linear-gradient(#d7dee8, #e7eef6);
  -webkit-box-shadow: inset 0 1px 0 rgba(0,0,0,0.05);
  box-shadow: inset 0 1px 0 rgba(0,0,0,0.05);
}

.stripe-connect.dark {
  background: #252525;
  background: rgba(0,0,0,0.5) !important;
}

/* Images*/

.stripe-connect span:before, .stripe-connect.blue span:before {
  background-image: url("data:image/png;base64,");
}

.stripe-connect.light-blue span:before {
  background-image: url("data:image/png;base64,");
}

/* Retina support */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
       only screen and (min--moz-device-pixel-ratio: 1.5),
       only screen and (min-device-pixel-ratio: 1.5) {

  .stripe-connect span:before, .stripe-connect.blue span:before {
    background-image: url("data:image/png;base64,");
  }

  .stripe-connect.light-blue span:before {
    background-image: url("data:image/png;base64,");
  }
}

